<template>
  <div>
    <swiper v-if="homeAds" :options="swiperOtions">
      <swiper-slide v-for="item in homeAds.items" :key="item._id">
        <a :href="item.url">
          <img class="w-100" :src="item.image" alt="">
        </a>
      </swiper-slide>
      <div class="swiper-pagination pagination-home text-right px-3 pb-1" slot="pagination"></div>
    </swiper>
    <!-- end of swiper -->
    <div class="nav-icons bg-white mt-3 text-center pt-3 text-grey-1">
      <ul class="flex-row flex-wrap ai-center">
        <li class="nav-item mb-3" v-for="item in guides" :key="item.iconName">
          <i :class="['sprite', 'sprite-' + item.iconName]"></i>
          <div class="py-2">{{item.title}}</div>
        </li>
      </ul>
      <div class="bg-light py-2 fs-sm">
        <i class="sprite sprite-around mr-1"></i>
        <span></span>
      </div>
    </div>
    <!-- end of icons -->

    <m-list-card title="" icon="menu" :categories="newsCats">
      <template #items="{ category }">
        <ul>
          <router-link
            v-for="(item, i) in category.newsList"
            :key="i"
            class="py-2 fs-lg flex-row"
            tag="li"
            :to="{ name: 'articles', params: {id: item._id} }">
            <span class="text-info">[{{item.categoryName}}]</span>
            <span class="px-2">|</span>
            <span class="flex-1 text-dark-1 text-ellipsis pr-2">{{item.title}}</span>
            <span class="text-dark fs-sm">{{item.createdAt | date}}</span>
          </router-link>
        </ul>
      </template>
    </m-list-card>

    <m-list-card title="" icon="hero" :categories="heroCats">
      <template #items="{ category }">
        <ul class="flex-row flex-wrap" style="margin: 0 -0.5rem;">
          <router-link
            tag="li"
            v-for="(item, i) in category.heroList" :key="i"
            :to="{ name: 'hero', params: { id: item._id } }"
            style="width: 20%;"
            class="p-2 text-center">
            <img :src="item.avatar" class="w-100" alt="">
            <div>{{item.name}}</div>
          </router-link>
        </ul>
      </template>
    </m-list-card>
    <!-- <m-card title="" icon="video"></m-card>
    <m-card title="" icon="article"></m-card> -->
  </div>
</template>

<script>
import moment from 'moment' // dayjs
export default {
  data () {
    return {
      swiperOtions: {
        pagination: {
          el: '.swiper-pagination'
        }
      },
      // 
      guides: [
        {
          title: '',
          iconName: 'news'
        },
        {
          title: '',
          iconName: 'story'
        },
        {
          title: '',
          iconName: 'store'
        },
        {
          title: '',
          iconName: 'beta'
        },
        {
          title: '',
          iconName: 'xrzq'
        },
        {
          title: '',
          iconName: 'whz'
        },
        {
          title: '',
          iconName: 'trz'
        },
        {
          title: '',
          iconName: 'wzyd'
        },
        {
          title: '',
          iconName: 'wx'
        },
        {
          title: '',
          iconName: 'version'
        }
      ],
      newsCats: [],
      heroCats: [],
      homeAds: null
    }
  },
  methods: {
    async fetchNewsList () {
      const res = await this.$http.get('/news/list')
      this.newsCats = res.data
    },
    async fetchHeroList () {
      const res = await this.$http.get('/heroes/list')
      this.heroCats = res.data
    },
    // 
    async fetchAds () {
      const res = await this.$http.get('/ads/home')
      this.homeAds = res.data
    }
  },
  created() {
    this.fetchNewsList()
    this.fetchHeroList()
    this.fetchAds()
  },
  filters: {
    date(val) {
      return moment(val).format('MM/DD')
    }
  }
}
</script>

<style lang="scss">
  @import '../../assets/css/variables';
  .pagination-home {
    .swiper-pagination-bullet {
      opacity: 1;
      border-radius: .153846rem /* 2/13 */;
      background: map-get($map: $colors, $key: 'white');
      &.swiper-pagination-bullet-active {
        background: map-get($map: $colors, $key: 'info');
      }
    }
  }

  .nav-icons {
    border-top: 1px solid $border-color;
    border-bottom: 1px solid $border-color;
    .nav-item {
      width: 25%;
      border-left: 1px solid $border-color;
      &:nth-child(4n + 1) {
        border-color: transparent;
      }
    }
  }

  .sprite-version {
    background: url(../../assets/images/version-icon.png) no-repeat 0 0;
    background-size: 1.538462rem /* 20/13 */;
    width: 1.615385rem /* 21/13 */;
    height: 1.846154rem /* 24/13 */;
  }

</style>
